import React from 'react';
import styled from '@emotion/styled';
import css from '@emotion/css';

import DefaultCover from '../assets/default-cover.svg';

interface ISupportRow {
  image: string;
  title: string | React.ReactNode;
  active: boolean | number;
}

const NoticeRow = ({ image, title, active }: ISupportRow) => {
  return (
    <Container active={active}>
      {image ? (
        <Image src={image} />
      ) : (
        <DefaultCoverWrap>
          <DefaultCover />
        </DefaultCoverWrap>
      )}
      <Title>{title}</Title>
    </Container>
  );
};

const Container = styled.div<{ active: boolean | number }>`
  display: flex;
  align-items: center;
  border-radius: 7px;
  background-color: #ffffff;
  padding: 12px 64px;
  margin-bottom: 10px;
  color: inherit;

  &:last-child {
    margin-bottom: 0;
  }

  html[color-mode='dark'] & {
    background-color: #1b1f28;
  }

  @media screen and (max-width: 500px) {
    padding: 20px 31px;
  }

  ${({ active }) =>
    !active &&
    css`
      background-color: rgba(0, 0, 0, 0.05);
      html[color-mode='dark'] & {
        background-color: #182b4a;
      }
    `}
`;

const Image = styled.img`
  width: 42px;
  height: 42px;
  border-radius: 2px;
  margin-right: 46px;

  @media screen and (max-width: 500px) {
    display: none;
  }
`;

const DefaultCoverWrap = styled.div`
  width: 42px;
  height: 42px;
  border-radius: 2px;
  margin-right: 46px;
  background-color: white;

  @media screen and (max-width: 500px) {
    display: none;
  }
`;

const Title = styled.span`
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  color: #1e1e1e;

  html[color-mode='dark'] & {
    color: white;
    font-weight: 300;

    b {
      font-weight: 500;
    }
  }

  @media screen and (max-width: 500px) {
    font-size: 13px;
    line-height: 1.28;
  }
`;

export default NoticeRow;
