import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';

import TextHeader from '../components/TextHeader';
import {
  StyledServiceWrapper,
  TopContents,
  BottomContainer,
  BottomContents,
} from '../components/SectionPage';
import NoticeRow from '../components/NoticeRow';

import { api } from '../utils/api';

import { IAlarm } from '../types';
import auth from '../utils/auth';

const Notice = () => {
  const [data, setData] = useState<Array<IAlarm>>([]);

  useEffect(() => {
    const fetchData = async () => {
      const {
        data: {
          data: { alarms },
        },
      } = await api.get('/alarm/index');
      setData(alarms);
    };

    const readAll = async () => {
      await api.post('/alarm/read/all');
      auth.setNoticeCount(0);
    };

    fetchData().then(() => readAll());
  }, []);

  // 읽음 처리 및 active state 컨트롤 필요

  return (
    <StyledServiceWrapper>
      <TopContents>
        <TextHeader>알림</TextHeader>
      </TopContents>
      <BottomContainer>
        <BottomContents>
          {!data.length && (
            <EmptyMessage>미확인 알림 메세지가 없습니다.</EmptyMessage>
          )}
          {data.map((alarm) => (
            <NoticeRow
              key={`notice-${alarm.id}`}
              image={alarm.image_url}
              title={
                <>
                  <b>{alarm.bold_text}</b>
                  {alarm.contents}
                </>
              }
              active={alarm.status}
            />
          ))}
        </BottomContents>
      </BottomContainer>
    </StyledServiceWrapper>
  );
};

export default Notice;

const EmptyMessage = styled.p`
  opacity: 0.8;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.63;
  text-align: center;
  color: #ffffff;
  margin-top: 168px;

  @media screen and (max-width: 500px) {
    margin-top: 118px;
  }
`;
